<template>
  <navbar 
  :lang="lang" 
  @getSearch="search = $event"
  @changeLang="changeLang"
  />
  <content-items :itemList="filterItemList"
  @changeItem="changeItem"
  @delItem="delItem" 
  :lang="lang"
  />
  <Modal v-show="openOrCloseM" 
  @closeModal="closeModal"
  @addItem="addItem"
  @editItem="editItem"
  :currentId="currentId"
  :edit="edit"
  :itemObj="itemObj"
  :lang="lang"
  />
  <addBtn @openModal="openModal" />
</template>
<script>
import Navbar from './components/Navbar.vue';
import ContentItems from './components/Contentitems.vue';
import Modal from './components/Modal.vue';
import addBtn from './components/addBtn.vue';
import langs from './langs.js'

export default {
  components: { Navbar, ContentItems, Modal, addBtn },
  data() {
    return {
      openOrCloseM: false,
      edit: false,
      itemList: [],
      currentId: 1,
      itemObj: {},
      search: '',
      lang: 'ru',
      langWords: {}
    }
  },
  computed: {
    filterItemList() {
      return this.search ?
        this.itemList.filter(item => item.title.toLowerCase().includes(this.search.toLowerCase())) :
        this.itemList
    }
  },
  methods: {
    openModal(bool) {
      this.openOrCloseM = bool
    },
    closeModal(bool) {
      this.openOrCloseM = bool
    },
    addItem(itemObj) {
      this.itemList.push(itemObj)
    },
    changeItem(id) {
      this.edit = this.openOrCloseM = true
      let obj = this.itemList.find((item) => item.id == id)
      this.itemObj = obj
      console.log(this.itemObj);
    },
    delItem(id) {
      this.itemList = this.itemList.filter(item => item.id != id)
    },
    editItem(obj) {
      this.itemList.forEach(item => {
        if(item.id == obj.id) {
          item.title = obj.title
          item.desc = obj.desc 
          item.date = obj.date
        }
      })
    },
    changeLang(value) {
      this.lang = localStorage.lang = value
    }
  },
  created() {
    if(localStorage.list) {
      const localList = localStorage.getItem('list')
      this.itemList = JSON.parse(localList)
      
      if(this.itemList.length >= 1) {
        this.currentId = this.itemList[this.itemList.length - 1].id
        this.currentId++
      }
    }
  },
  beforeCreate() {
    if (!localStorage.lang) {
      localStorage.setItem('lang', 'ru')
    }

    this.lang = localStorage.lang || 'ru'
    this.langWords = langs
    localStorage.words = JSON.stringify(this.langWords)
  },
  watch: {
    itemList: {
      handler(updateList) {
        localStorage.setItem('list', JSON.stringify(updateList))
      },
      deep: true
    }
  },
  provide() {
    return {
      words: JSON.parse(localStorage.words)
    }
  }
  
}
</script>