<template>
  <nav class="navbar">
    <div class="navbar__nav container" v-if="navbar">
      <button class="navbar-btn btn-lang" @click="changeLang" v-if="lang == 'en'">Ru</button>
      <button class="navbar-btn btn-lang" @click="changeLang" v-else>En</button>
      <h1 class="navbar__nav--title">{{ words.navbarTitle[lang] }}</h1>
      <button class="navbar-btn btn-search" @click.prevent="navbar = !navbar">
        <img src="@/assets/images/search.png" alt="search">
      </button>
    </div>
    <div class="navbar__search container" v-else>
      <form class="navbar__search--form">
        <button class="navbar-btn back" @click.prevent="navbar = !navbar">
        <img src="@/assets/images/back.svg" alt="">
      </button>
      <input type="text" :placeholder="words.navbarSearch[lang]" class="navbar__search--input" v-model="search">
      <button class="navbar-btn clear" @click.prevent="search = ''">
        <img src="@/assets/images/clear.svg" alt="">
      </button>
      </form>
    </div>
  </nav>
</template>
<script>
export default {
  props: ['lang'],
  inject: ['words'],
  data() {
    return {
      navbar: true,
      search: '',
    }
  },
  methods: {
    changeLang() {
      this.$emit('changeLang', this.lang == 'ru' ? 'en' : 'ru')
    }
  },
  watch: {
    search(val) {
      this.$emit('getSearch', val)
    },
  }
}
</script>